import React from "react";
import "./SideBar.css";
import { Nav } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faGlobeAsia,
  faBookReader,
  faBookMedical,
  faUser,
} from "@fortawesome/free-solid-svg-icons";
import { Link } from "react-router-dom";

export default function SideBar(props) {
  return (
    <div className="side-bar-wrapper">
      <div className="sidebar-header">
        <h3>
          <span>
            <FontAwesomeIcon icon={faGlobeAsia} size="sm" /> Online Course
          </span>
        </h3>
      </div>
      <Nav className="nav-wrapper">
        <Nav.Link>
          <Link to="/">
            <span>
              <FontAwesomeIcon className="icon-sidebar" icon={faBookReader} />{" "}
              Overview
            </span>
          </Link>
        </Nav.Link>
        <Nav.Link>
          <Link to="/category">
            <span>
              <FontAwesomeIcon className="icon-sidebar" icon={faBookMedical} />{" "}
              Category
            </span>
          </Link>
        </Nav.Link>
        <Nav.Link>
          <Link to="/user">
            <span>
              <FontAwesomeIcon className="icon-sidebar" icon={faUser} /> User
            </span>
          </Link>
        </Nav.Link>
        <Nav.Link>
          <Link to="/course">
            <span>
              <FontAwesomeIcon className="icon-sidebar" icon={faBookReader} />{" "}
              Course
            </span>
          </Link>
        </Nav.Link>
      </Nav>
    </div>
  );
}
